.Navbar {
  width: 100%;
  padding: 0.5rem 0;
  background-color: rgb(221, 221, 221);
  text-align: center;
}

.Navbar__link {
  margin: 0 10px;
}

.Home {
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
}

.Home__avatar {
  background-image: url(https://image.flaticon.com/icons/svg/1141/1141797.svg);
  background-size: 70px;
  width: 80px;
  height: 80px;
  background-repeat: no-repeat;
  background-color: #d9dbdd;
  background-position: 5px 10px;
  margin-bottom: 14px;
}

.img-card-projects {
  /*background-color: whitesmoke;*/
  width: 75px;
  height: 75px;
  position: absolute;
  top: -2.3rem;
  left: 0;
  right: 0;
  display: block;
  margin: 0 auto;
}

.img-card-platzi {
  background-color: whitesmoke;
  width: 75px;
  height: 75px;
}

body {
  background-color: #fafafa;
  font-family: "Noto Sans SC", sans-serif;
}

html,
* {
  margin: 0;
}

.container {
  padding-bottom: 20px;
}

